import React, { useEffect, useState } from "react";
import Resources from "./Resources";
import ResourceEditable from "./ResourceEditable";
import axios from "axios";
import { environment } from "../../environments/environment";
import Loader from "../Loader/Loader";
import { Refresh } from "@mui/icons-material";

function VendorManagSummaryTable(props) {
  const {
    summaryDataNw,
    formData,
    formattedFromDate,
    toDateFromDate,
    setRefresh,
    refresh,
    validationM,
    alldepartments,
    allcountries,
    abortController,
    setValidation,
  } = props;
  console.log(abortController, "abortController");

  const [data, setData] = useState([]);
  const [displayTable, setDisplayTable] = useState(null);
  const [displayHeader, setDisplayHeaders] = useState(null);
  const [open, setOpen] = useState(false);
  const [headerData, setHeaderData] = useState([]);
  const [bodyData, setBodyData] = useState([]);
  const [searchapidata, setSearchApiData] = useState([]);
  const [tableTitle, srtTableTitle] = useState();
  const [colorFilter, setColorFilter] = useState([]);
  const [contractIds, setContractIds] = useState([{}]);
  const [value, setValue] = useState(false);
  const [summaryData, setSummaryData] = useState(summaryDataNw);
  const [openNw, setOpenNw] = useState(false);

  const searchHandle = () => {
    console.log("issuesolve");
    axios({
      method: "post",
      url: baseUrl + `/VendorMS/management/getVendManagementSummary`,
      data: {
        buIds: alldepartments,
        country: allcountries,
        fromDate: formattedFromDate,
        toDate: toDateFromDate,
        isExport: 0,
      },

      headers: { "Content-Type": "application/json" },
    }).then((response) => {
      console.log(response.data, "response.data");
      setSummaryData(response.data);
      //displayTableFnc();
    });
  };

  //setContractIds
  console.log(tableTitle);
  console.log(data);

  const baseUrl = environment.baseUrl;
  let flag = 1;

  let tabHeaders = [
    "name",
    "total_hc",
    "contract",
    "contract_hire",
    "freelancer",
    "fixed_bid",
    "offered",
    "on_exit_path",
    "conv_in_prog",
    "awaiting_conv",
  ];

  useEffect(() => {
    displayTableFnc();
  }, [summaryData]);

  useEffect(() => { }, [setData]);

  const getresourcedtls = (skillType) => {
    abortController.current = new AbortController();
    axios({
      method: "post",
      url: baseUrl + `/VendorMS/management/getVendManagementResourceDtls`,
      signal: abortController.current.signal,
      data: {
        buIds: formData.buIds,
        country: formData.country,
        fromDate: formattedFromDate,
        toDate: toDateFromDate,
        lkKey: skillType.a,
        skillId: skillType.b,
        isExport: 0,
        vendorId: -1,
        page: "vmg",
        custId: "0",
        projId: "0",
        buId: "0",
      },

      headers: { "Content-Type": "application/json" },
    }).then((response) => {
      var response = response.data;
      let hData = [];
      let bData = [];
      for (let index = 0; index < response.length; index++) {
        if (index == 0) {
          hData.push(response[index]);
        } else {
          bData.push(response[index]);
        }
      }
      /*setTimeout(() => {
        setRefresh(false);
        setOpen(true);
      }, 1000);*/
      setHeaderData(hData);
      setBodyData(bData);
      setData(response);
      setSearchApiData(bData);
      setOpenNw(true);
      //color sorting

      let firstColor,
        secondColor,
        thirdColor = "",
        count = 0;
      firstColor = bData[0].alloc_contract_date_icon?.split("~")[0];
      secondColor = "";
      console.log(firstColor, "firstColor");
      for (let index = 0; index < bData.length; index++) {
        let colorFind = bData[index].alloc_contract_date_icon?.split("~")[0];
        if (firstColor != colorFind && count == 0) {
          secondColor = colorFind;
          count++;
        } else if (firstColor != colorFind && secondColor != colorFind) {
          thirdColor = colorFind;
          break;
        }
      }
      const colorFilter = [firstColor, secondColor, thirdColor];
      setColorFilter(colorFilter);
    });
  };
  useEffect(() => {
    setOpen(true);
    setRefresh(false);
  }, [data]);

  const getContractId = () => {
    axios
      .get(`http://10.11.12.149:8090/VendorMS/management/getContractTypes`)

      .then((Response) => {
        let data = Response.data;
        setContractIds(data);
        console.log(data, "cidResponse");
      })
      .catch((error) => console.log(error));
  };

  const onclickHandler = (a, b) => {
    setValidation("");
    setOpenNw(false);
    setRefresh(false);
    // a --- contract type
    // b --- skill type

    const temp = {};
    temp["a"] = a;
    temp["b"] = b;
    getresourcedtls(temp);
    getContractId();
    srtTableTitle(a);
    console.log(a);
    console.log(b);
  };

  const displayTableFnc = () => {
    setDisplayHeaders(() => {
      return summaryData.map((element, index) => {
        let tabHeader = [];
        tabHeaders.forEach((inEle, inInd) => {
          if (index === 0) {
            let value = ("" + element[inEle]).includes("^&1")
              ? element[inEle].replaceAll("^&1", " ")
              : element[inEle];
            tabHeader.push(
              <th
                key={inInd}
                style={{
                  textAlign: "center",
                }}
                title={element[inEle]}
              >
                {value}
              </th>
            );
          }
        });
        return <tr key={index}>{tabHeader}</tr>;
      });
    });
    setDisplayTable(() => {
      return summaryData.slice(1).map((element, index) => {
        let tabData = [];
        tabHeaders.forEach((inEle, inInd) => {
          if (index === 0) {
            tabData.push(
              <td align={inInd > 0 ? "right" : "left"}>
                <b>
                  <span
                    style={{
                      cursor: inInd > 0 && element[inEle] != 0 ? "pointer" : "",
                      color: inInd != 0 && element[inEle] != 0 ? "#2e88c5" : "",
                    }}
                    onClick={() => {
                      inInd > 0 && element[inEle] != 0
                        ? onclickHandler(inEle, element.id)
                        : "";
                    }}
                    className="summary"
                    title={element[inEle]}
                  >
                    {element[inEle]}
                  </span>
                </b>
              </td>
            );
          } else {
            tabData.push(
              <td align={inInd > 0 ? "right" : "left"}>
                <span
                  style={{
                    cursor: inInd > 0 && element[inEle] != 0 ? "pointer" : "",
                    color: inInd != 0 && element[inEle] != 0 ? "#2e88c5" : "",
                  }}
                  onClick={() => {
                    inInd > 0 && element[inEle] != 0
                      ? onclickHandler(inEle, element.id)
                      : "";
                  }}
                  title={element[inEle]}
                >
                  {element[inEle]}
                </span>
              </td>
            );
          }
          // }
        });
        return (
          <tr
            style={{
              backgroundColor:
                index === 1
                  ? "#f5d5a7 "
                  : index === 0
                    ? "#f4f4f4"
                    : "#d8eaeac4",
            }}
            key={index}
          >
            {tabData}
          </tr>
        );
      });
    });
  };
  const validationMsg = (v) => {
    validationM(v);
  };
  return (
    <div className="my-3 darkHeader">
      <table className="table table-bordered table-striped htmlTable ">
        <thead>{displayHeader}</thead>
        <tbody>{displayTable}</tbody>
      </table>
      <div>
        {console.log(bodyData)}
        {open === true && openNw == true && formData.viewBy == "view" ? (
          <Resources
            setRefresh={setRefresh}
            refresh={Refresh}
            tableTitle={tableTitle}
            flag={flag}
            headerData={headerData}
            bodyDataa={bodyData}
            setBodyData={setBodyData}
            colorFilterR={colorFilter}
          />
        ) : (
          ""
        )}

        {open === true && openNw == true && formData.viewBy == "Edit" ? (
          <ResourceEditable
            setRefresh={setRefresh}
            refresh={Refresh}
            tableTitle={tableTitle}
            flag={flag}
            headerData={headerData}
            bodyDataa={bodyData}
            setBodyData={setBodyData}
            colorFilter={colorFilter}
            summaryData={summaryData}
            validationMsg={validationMsg}
            contractIds={contractIds}
            searchHandle={searchHandle}
          />
        ) : (
          ""
        )}
      </div>
    </div>
  );
}

export default VendorManagSummaryTable;
