import React, { useState, useEffect } from "react";
import InnerProfile from "./Profile";
import InnerOpen from "./InnerOpen";
import InnerSearch from "./InnerSearch";
import RProfile from "../ResourceProfile/RProfile";

function Profile() {
  let topMenus = ["Profile", "Search", "Open"];
  const loggedUserId = localStorage.getItem("resId");
  const loggedResId = parseInt(loggedUserId) + 1;
  const [resourceId, setResourceId] = useState(loggedResId);
  let url = window.location.href;
  let teamArr = url.split(":");
  
  // Separate state variable to track the "Open" tab
  // const [openTabButtonState, setOpenTabButtonState] = useState("Open");

  // const [buttonState, setButtonState] = useState(() => {
  //   // Retrieve the last selected tab from localStorage on component mount
  //   return localStorage.getItem("selectedProfileTab")?.split("+")[1] === teamArr[teamArr.length - 1] ? localStorage.getItem("selectedProfileTab").split("+")[0] : topMenus[0] ?? topMenus[0];
  // });

  const [buttonState, setButtonState] = useState(topMenus[0]);

  console.log(resourceId);
  // Save the selected tab to localStorage whenever it changes
  // useEffect(() => {
  //   localStorage.setItem("selectedProfileTab", buttonState + "+" + teamArr[teamArr.length - 1]);
  // }, [buttonState]);
  useEffect(() => {
    if (url.includes("profile")) {
      setButtonState("Profile");
    }

    let teamArr = url.split(":");
    setResourceId(teamArr[teamArr.length - 1]);
  }, []);

  const handleTabClick = (tab) => {
    setButtonState(tab);
  };

  return (
    <div>
      <div className="tabs">
        {topMenus.map((data, index) => {
          return (
            <button
              className={
                buttonState === data ? "buttonDisplayClick" : "buttonDisplay"
              }
              onClick={() => {
                handleTabClick(data); // Update the selected tab when clicked
              }}
              key={index}
            >
              {data}
            </button>
          );
        })}
      </div>
      {buttonState === "Profile" && (
        <RProfile resId={resourceId} setResourceId={setResourceId} />
      )}
      {buttonState === "Search" && <InnerSearch />}
      {buttonState === "Open" && (
        <InnerOpen
          setButtonState={setButtonState} // Pass the handler
          // currentOpenTabButtonState={openTabButtonState} // Pass the current "Open" tab selection
        />
      )}
    </div>
  );
}

export default Profile;
