import React, { useEffect, useRef, useState } from "react";
import { FilterMatchMode, FilterOperator } from "primereact/api";
import { DataTable } from "primereact/datatable";
import { InputText } from "primereact/inputtext";
import ExcelJS from "exceljs";

// import "./ErrorLogsTable.scss";

function VendorSearchTable(props) {
  const {
    data,
    rows,
    col,
    headerdatagroup,
    linkColumns,
    linkColumnsRoutes,
    dynamicColumns,
    headerData,
    setHeaderData,
    exportData,
    customFilters,
    customFilterValue,
    setCustomFilterValue,
    fileName,
  } = props;
  const [mainData, setMainData] = useState([]);
  const [bodyData, setBodyData] = useState([]);
  // const [exportColumns, setExportColumns] = useState([]);
  const dt = useRef(null);
  useEffect(() => {
    console.log(data);
    setMainData(JSON.parse(JSON.stringify(data)));
  }, [data]);

  useEffect(() => {
    if (mainData.length > 0) {
      setHeaderData(mainData[0]);
      setBodyData(mainData.splice(1));

      let dtt = [];
      let headDt = mainData[0];

      Object.keys(headDt).forEach((d) => {
        dtt.push({ title: d, dataKey: headDt[d] });
      });
      console.log(mainData);
    }
  }, [mainData]);

  const filtersData = {
    contains: { value: null, matchMode: FilterMatchMode.CONTAINS },
  };

  const [filters1, setFilters1] = useState({
    global: filtersData["contains"],
  });

  useEffect(() => {
    setFilters1({
      global: filtersData["contains"],
    });
  }, [headerData]);
  const [globalFilterValue1, setGlobalFilterValue1] = useState("");

  const clearFilter1 = () => {
    initFilters1();
  };

  const initFilters1 = () => {
    setGlobalFilterValue1("");
  };

  const onGlobalFilterChange1 = (e) => {
    const value = e.target.value;
    let _filters1 = { ...filters1 };
    _filters1["global"].value = value;
    setFilters1(_filters1);
    setGlobalFilterValue1(value);
  };

  const RenderCustomFilters = () => {
    let cstFtlrs = customFilters;
    switch (cstFtlrs["type"]) {
      case "select":
        return (
          <select
            id={cstFtlrs["id"]}
            onChange={(e) => {
              setCustomFilterValue(e.target.value);
            }}
          >
            {Object.keys(cstFtlrs["data"]).map((d) => {
              return (
                <option
                  selected={customFilterValue == d ? true : false}
                  value={d}
                >
                  {cstFtlrs["data"][d]}
                </option>
              );
            })}
          </select>
        );
        break;

      default:
        break;
    }
  };
  const exportCSV = (selectionOnly) => {
    dt.current.exportCSV({ selectionOnly });
  };
  const saveAsExcelFile = (buffer, fileName) => {
    import("file-saver").then((module) => {
      if (module && module.default) {
        let EXCEL_TYPE =
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8";
        let EXCEL_EXTENSION = ".xlsx";
        const data = new Blob([buffer], { type: EXCEL_TYPE });
        module.default.saveAs(data, fileName + EXCEL_EXTENSION);
      }
    });
  };
  const exportExcel = () => {
    import("xlsx").then((xlsx) => {
      const headers = Object.keys(data[0]);
      const uniqueHeaders = [...new Set(headers)];
      const worksheetData = data.map((item) =>
        uniqueHeaders.map((header) => item[header])
      );
      const dataRows = worksheetData.map((item) => Object.values(item));
      const workbook = new ExcelJS.Workbook();
      const worksheet = workbook.addWorksheet(fileName);
      for (let i = 0; i < dataRows.length; i++) {
        const row = worksheet.addRow(dataRows[i]);
      }
      const boldRow = [1];
      boldRow.forEach((index) => {
        const row = worksheet.getRow(index);
        row.font = { bold: true };
      });
      workbook.xlsx.writeBuffer().then((buffer) => {
        const blob = new Blob([buffer], {
          type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
        });
        saveAs(blob, fileName);
      });
      // const worksheet = xlsx.utils.aoa_to_sheet(worksheetData, {
      //   skipHeader: true,
      // });
      // const workbook = { Sheets: { data: worksheet }, SheetNames: ["data"] };
      const excelBuffer = xlsx.write(workbook, {
        bookType: "xlsx",
        type: "array",
      });
      saveAsExcelFile(excelBuffer, fileName);
    });
  };
  const exportColumns = [
    { dataKey: "vendorId", title: "Vendor Id", width: 27 },
    { dataKey: "vendor_name", title: "Vendor Name", width: 25 },
    { dataKey: "contactName", title: "Contact Name", width: 40 },
    { dataKey: "phone", title: "Phone", width: 40 },
    { dataKey: "email", title: "Email", width: 40 },
    { dataKey: "contryName", title: "Country Name", width: 30 },
    { dataKey: "nxtRvwDt", title: "Next Review Date", width: 25 },
    { dataKey: "signedDt", title: "Contract Signed Date", width: 25 },
  ];
  console.log(data);
  const exportPdf = () => {
    import("jspdf").then((jsPDF) => {
      import("jspdf-autotable").then(() => {
        const doc = new jsPDF.default();
        const headline = "PPM :: VMG Search";
        doc.text(headline, 14, 15);
        const modifiedData = [...data.slice(1)];
        doc.autoTable({
          columns: exportColumns,
          body: modifiedData,
          startY: 20,
        });
        doc.save("PPM VMG.pdf");
      });
    });
  };
  const printTable = () => {
    openPrintWindow();
  };
  const openPrintWindow = () => {
    const printWindow = window.open("", "_blank");
    if (!printWindow) return;
    const tableContent = generatePrintTableContent();
    printWindow.document.write(`
    <html>
      <head>
        <title>Print Table</title>
        <link rel="stylesheet" type="text/css" href="path-to-your-print-styles.css">
      </head>
      <body>
        <h1>PPM :: VMG Search</h1>
        ${tableContent}
      </body>
    </html>
  `);
    printWindow.document.close();
    printWindow.print();
  };
  const generatePrintTableContent = () => {
    const tableRows = data.map(
      (data) =>
        `<tr>
      <td>${data.vendorId}</td>
      <td>${data.vendor_name}</td>
      <td>${data.contactName}</td>
      <td>${data.phone}</td>
      <td>${data.email}</td>
      <td>${data.contryName}</td>
      <td>${data.nxtRvwDt}</td>
      <td>${data.signedDt}</td>
      <td>${data.expireDt}</td>
      <td>${data.website}</td>
    </tr>`
    );
    return `
    <table>
      <thead>
        <tr>
       
        </tr>
      </thead>
      <tbody>
        ${tableRows.join("")}
      </tbody>
    </table>
  `;
  };

  const renderHeader1 = () => {
    return (
      <div className="flex  flex-row-reverse">
        {customFilters != undefined && (
          <span>
            <RenderCustomFilters />
          </span>
        )}
        <div className="exportBtn ml-3">
          {exportData?.includes("CSV") && (
            <span
              className="pi pi-file csv"
              onClick={() => exportCSV(false)}
              title="CSV"
            />
          )}
          {exportData?.includes("XLS") && (
            <span
              className="pi pi-file-excel excel"
              onClick={exportExcel}
              title="Export to Excel"
            />
          )}
          {exportData?.includes("PDF") && (
            <span
              className="pi pi-file-pdf pdf"
              onClick={exportPdf}
              title="PDF"
            />
          )}
          {exportData?.includes("PRINT") && (
            <span
              className="pi pi-print print"
              onClick={printTable}
              title="Print"
            />
          )}
        </div>
        <span className="p-input-icon-left tableGsearch">
          <i className="pi pi-search" />
          <InputText
            defaultValue={globalFilterValue1}
            onChange={onGlobalFilterChange1}
            placeholder="Keyword Search"
          />
        </span>
      </div>
    );
  };
  const header1 = renderHeader1();
  return (
    <div className="darkHeader">
      {Object.keys(headerData).length > 0 && (
        <DataTable
          paginatorTemplate="RowsPerPageDropdown FirstPageLink PrevPageLink CurrentPageReport NextPageLink LastPageLink"
          currentPageReportTemplate="{first} to {last} of {totalRecords}"
          rowsPerPageOptions={[10, 25, 50]} //------------->
          value={bodyData}
          paginator
          sortMode={false}
          showGridlines
          rows={rows}
          dataKey="id"
          filters={filters1}
          // responsiveLayout="scroll"
          header={header1}
          emptyMessage="No Records found."
          ref={(el) => {
            dt.current = el;
          }}
          className="primeReactDataTable vendorResourceTable"
        >
          {dynamicColumns}
        </DataTable>
      )}
    </div>
  );
}
export default VendorSearchTable;
