import React, { useEffect, useState, useRef } from "react";
import { environment } from "../../environments/environment";
import { MultiSelect } from "react-multi-select-component";
import axios from "axios";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import moment from "moment";
import VendorPerformanceTable from "./VendorPerformanceTable";
import {
  FaCaretDown,
  FaChevronCircleDown,
  FaChevronCircleUp,
  FaSearch,
} from "react-icons/fa";
import { CCollapse } from "@coreui/react";
import "./VendorManagement.scss";
import GlobalValidation from "../ValidationComponent/GlobalValidation";
import Loader from "../Loader/Loader";
import ScreenBreadcrumbs from "../Common/ScreenBreadcrumbs";
//import VendorPerformanceResourceTable from "./VendorPerformanceResourceTableSahid";

function VendorPerformanceFilters(props) {
  const [vendor, setVendor] = useState([]);
  const [selectedVendor, setSelectedVendor] = useState([]);
  const [country, setCountry] = useState([]);
  const [selectedCountry, setSelectedCountry] = useState([]);
  const [date, setDate] = useState(new Date());
  const [performanceData, setPerformanceData] = useState([]);
  const [open, setOpen] = useState(false);
  const [loader, setLoader] = useState(false);
  const abortController = useRef(null);

  const ref = useRef([]);
  const [visible, setVisible] = useState(false);
  const [cheveronIcon, setCheveronIcon] = useState(FaChevronCircleUp);
  const [validator, setValidator] = useState(false);
  const [openNw, setOpenNw] = useState(false);
  const loggedUserId = localStorage.getItem("resId");
  const [routes, setRoutes] = useState([]);
  let textContent = "Vendors";
  let currentScreenName = ["Resource Vendor Performance"];

  const baseUrl = environment.baseUrl;
  let allCountry;
  let allVendor;

  const customValueRenderer = (selected, _options) => {
    //console.log(selected);
    //console.log(country.length);
    return selected.length === country.length ||
      selected.length === vendor.length
      ? "<< ALL >>"
      : selected.length === 0
        ? "<< Please Select >>"
        : selected.map((label) => {
          return selected.length > 1 ? label.label + "," : label.label;
        });
  };
  // const[duration,setDuration]=useState([])
  // const[viewBy,setViewBy]=useState([])
  //var date = new Date();
  let converteddate = moment(date).startOf("month").format("yyyy-MM-DD");
  const initialValue = {
    vendorList: "",
    month: converteddate,
    duration: "6",
    countryIds: "",
    summary: "vendor",
  };

  const [formData, setFormData] = useState(initialValue);

  //const baseUrl = environment.baseUrl;
  const handleChangeDate = (date) => {
    setDate(date);
  };

  const FilterChangeHandler = (e) => {
    const { id, value } = e.target;
    setFormData((prevVal) => ({ ...prevVal, [id]: value }));
  };

  const getCountries = () => {
    axios
      .get(`http://10.11.12.149:8090/CostMS/cost/getCountries`)

      .then((Response) => {
        let countries = [];
        let data = Response.data;
        data.length > 0 &&
          data.forEach((e) => {
            let countryObj = {
              label: e.country_name,
              value: e.id,
            };
            countries.push(countryObj);
          });
        setCountry(countries);
        setSelectedCountry(countries);
      })
      .catch((error) => console.log(error));
  };

  useEffect(() => {
    let countryList = [];
    country.forEach((d) => {
      countryList.push(d.value);
    });
    setFormData((prevVal) => ({
      ...prevVal,
      ["country"]: countryList.toString(),
    }));
  }, [country]);

  const getVendors = () => {
    axios
      .get(`http://10.11.12.149:8090/VendorMS/vendor/getVendorsNameandId`)

      .then((Response) => {
        let vendors = [];
        let data = Response.data;
        data.length > 0 &&
          data.forEach((e) => {
            let vendorObj = {
              label: e.name,
              value: e.id,
            };
            vendors.push(vendorObj);
          });
        setVendor(vendors);
        setSelectedVendor(vendors);
      })
      .catch((error) => console.log(error));
  };

  const ArrowRenderer = ({ expanded }) => (
    <>
      {expanded ? (
        <FaCaretDown className="chevronIcon" />
      ) : (
        <FaCaretDown className="chevronIcon" />
      )}
    </>
  );

  const getMenus = () => {
    // setMenusData

    axios({
      method: "GET",
      url: baseUrl + `/CommonMS/master/getMenus?loggedUserId=${loggedUserId}`,
    }).then((resp) => {
      let data = resp.data;

      data.forEach((item) => {
        if (item.display_name === textContent) {
          setRoutes([item]);
        }
      });
    });
  };

  useEffect(() => {
    let vendorsList = [];
    vendor.forEach((d) => {
      vendorsList.push(d.value);
    });
    setFormData((prevVal) => ({
      ...prevVal,
      ["vendor"]: vendorsList.toString(),
    }));
  }, [vendor]);

  useEffect(() => {
    getCountries();
    getVendors();
    getMenus()
  }, []);

  const handlesearch = (e) => {
    // setValidator(true);
    //console.log(converteddate, "converteddate");
    setFormData((prevVal) => ({
      ...prevVal,
      month: converteddate,
    }));

    // console.log(formData.month, "formData.monthafterclick");
    setOpen(true);
    let valid = GlobalValidation(ref);
    //console.log(valid);

    if (valid) {
      setValidator(true);
      return;
    }
    // const countryArray = formData.country.split(",");
    allCountry =
      selectedCountry.length === country.length ? "-1" : formData.countryIds;
    // const vendorArray = formData.vendor.split(",");
    //console.log(formData.countryIds, "insidevpafclcountry");
    //console.log(formData.vendorList, "formData.vendorList");
    allVendor =
      selectedVendor.length === vendor.length ? "-1" : formData.vendorList;
    // console.log(selectedVendor);
    //console.log(formData.summary, "formData.summary");
    abortController.current = new AbortController();

    axios({
      method: "post",
      url: `http://10.11.12.149:8090/VendorMS/performance/getVendorperformance`,
      signal: abortController.current.signal,
      data: {
        vendorList: allVendor,
        month: converteddate,
        duration: formData.duration,
        countryIds: allCountry,
        summary: formData.summary,
      },

      headers: { "Content-Type": "application/json" },
    })
      .then((response) => {
        var resp = response.data;
        setVisible(!visible);
        setPerformanceData(resp);
        setOpenNw(true);
      })
      .catch((e) => {
        console.log(e);
      });
    setLoader(true);
  };
  useEffect(() => {
    if (openNw == true) {
      setLoader(false);
    }
  }, [openNw]);

  const handleAbort = () => {
    abortController.current && abortController.current.abort();
    setLoader(false);
  };

  return (
    <div>
      {validator ? (
        <div className="statusMsg error">
          <span>Please Select Mandatory Fields</span>
        </div>
      ) : (
        ""
      )}

      <div className="col-md-12">
        <div className="pageTitle">
          <div className="childOne"></div>
          <div className="childTwo">
            <h2>Resource Vendor Performance</h2>
          </div>
          <div className="childThree"></div>
        </div>
      </div>
      <ScreenBreadcrumbs
        routes={routes}
        currentScreenName={currentScreenName}
      />
      <div class="group mb-3 customCard">
        <div className="col-md-12 collapseHeader">
          <h2>Search Filters</h2>
          <div
            onClick={() => {
              setVisible(!visible);
              visible
                ? setCheveronIcon(FaChevronCircleUp)
                : setCheveronIcon(FaChevronCircleDown);
            }}
          >
            <span>{cheveronIcon}</span>
          </div>
        </div>
        <CCollapse visible={!visible}>
          <>
            <div className="group-content row mt-2">
              <div className="col-md-3 mb-2 ">
                <div className="row">
                  <label className="col-5 ">
                    Vendors<span className="required error-text ml-1"> *</span>{" "}
                  </label>
                  <span className="col-1 p-0">:</span>
                  <div
                    className="col-6 multiselect"
                    ref={(ele) => {
                      ref.current[0] = ele;
                    }}
                    id="Vendors"
                  >
                    <MultiSelect
                      ArrowRenderer={ArrowRenderer}
                      id="Vendors"
                      options={vendor}
                      hasSelectAll={true}
                      isLoading={false}
                      shouldToggleOnHover={false}
                      disableSearch={false}
                      value={selectedVendor}
                      disabled={false}
                      valueRenderer={customValueRenderer}
                      onChange={(s) => {
                        setSelectedVendor(s);
                        let filteredValues = [];
                        s.forEach((d) => {
                          filteredValues.push(d.value);
                        });

                        setFormData((prevVal) => ({
                          ...prevVal,
                          ["vendorList"]: filteredValues.toString(),
                        }));
                      }}
                    />
                  </div>
                </div>
              </div>

              <div className="col-md-3 mb-2">
                <div className="row">
                  <label className="col-5">Month </label>
                  <span className="col-1 p-0">:</span>
                  <div className="col-6 ">
                    <DatePicker
                      id="month"
                      selected={moment(date)._d}
                      onChange={(e) => {
                        handleChangeDate(e);
                      }}
                      dateFormat="MMM-yyyy"
                      showMonthYearPicker
                      onKeyDown={(e) => {
                        e.preventDefault();
                      }}
                    />
                  </div>
                </div>
              </div>

              <div className="col-md-3 mb-2">
                <div className="row">
                  <label className="col-md-5">Duration </label>
                  <span className="col-1 p-0">:</span>
                  <div className="col-6">
                    <select
                      id="duration"
                      onChange={(e) => FilterChangeHandler(e)}
                      name="duration"
                      className="col-md-12 col-sm-12 col-xs-12 "
                      defaultValue={"6"}
                    >
                      <option value="1">1</option>
                      <option value="2">2</option>
                      <option value="3">3</option>
                      <option value="4">4</option>
                      <option value="5">5</option>
                      <option value="6">6</option>
                    </select>
                  </div>
                </div>
              </div>

              <div className="col-md-3 mb-2">
                <div className="row">
                  <label className="col-5 ">
                    Country<span className="required error-text ml-1"> *</span>{" "}
                  </label>
                  <span className="col-1 p-0">:</span>
                  <div
                    className="col-6 multiselect"
                    ref={(ele) => {
                      ref.current[1] = ele;
                    }}
                    id="countryids"
                  >
                    <MultiSelect
                      ArrowRenderer={ArrowRenderer}
                      id="country"
                      options={country}
                      hasSelectAll={true}
                      value={selectedCountry}
                      disabled={false}
                      valueRenderer={customValueRenderer}
                      onChange={(e) => {
                        setSelectedCountry(e);
                        let filteredCountry = [];
                        e.forEach((d) => {
                          filteredCountry.push(d.value);
                        });
                        setFormData((prevVal) => ({
                          ...prevVal,
                          ["countryIds"]: filteredCountry.toString(),
                        }));
                      }}
                      labelledBy={"countries"}
                    />
                  </div>
                </div>
              </div>

              <div className="col-md-3 mb-2">
                <div className="row">
                  <label className="col-5 ">ViewBy </label>
                  <span className="col-1 p-0">:</span>
                  <div className="col-6">
                    <select
                      id="summary"
                      onChange={(e) => FilterChangeHandler(e)}
                      name="summary"
                      defaultValue={"vendor"}
                    >
                      <option value="vendor">Vendor</option>
                      <option value="customer">Customer</option>
                      <option value="bu">BU</option>
                    </select>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-12 col-sm-12 col-xs-12 my-2 btn-container center">
              <button className="btn btn-primary" onClick={() => {
                setOpenNw(false);
                handlesearch();

              }}>
                <FaSearch />
                Search
              </button>
            </div>
          </>
        </CCollapse>
      </div>
      {loader ? <Loader handleAbort={handleAbort} /> : ""}
      {open === true && openNw == true ? (
        <VendorPerformanceTable
          performanceData={performanceData}
          formData={formData}
        />
      ) : (
        ""
      )}
    </div>
  );
}

export default VendorPerformanceFilters;
