import React, { useEffect, useState, useRef } from "react";
import { MultiSelect } from "react-multi-select-component";
import axios from "axios";
import { environment } from "../../environments/environment";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import VendorManagSummaryTable from "./VendorManagSummaryTable";
import moment from "moment";
import "./VendorManagement.scss";
import { CCollapse } from "@coreui/react";
import {
  FaChevronCircleDown,
  FaChevronCircleUp,
  FaSearch,
  FaCaretDown,
} from "react-icons/fa";
import { FaCheck } from "react-icons/fa";
import Loader from "../Loader/Loader";
import { AiFillWarning } from "react-icons/ai";
import ScreenBreadcrumbs from "../Common/ScreenBreadcrumbs";
import GlobalHelp from "../PrimeReactTableComponent/GlobalHelp";

function VendorManagementFilters(props) {
  var sdate = moment().subtract(7, "days").format("DD-MMM-YYYY");

  const [startDate, setStartDate] = useState(moment(sdate)._d);
  const [validator, setValidator] = useState(false);
  const [endDate, setendDate] = useState(new Date());
  const [country, setCountry] = useState([]);
  const [selectedCountry, setSelectedCountry] = useState([]);
  const [departments, setDepartments] = useState([]);
  const [selectedDepartments, setSelectedDepartments] = useState([]);
  const [open, setOpen] = useState(false);
  const [summaryData, setSummaryData] = useState([]);
  const [visible, setVisible] = useState(false);
  const [cheveronIcon, setCheveronIcon] = useState(FaChevronCircleUp);
  const [loader, setLoader] = useState(false);
  const [refresh, setRefresh] = useState(false);
  const [formDataNew, setFormDataNew] = useState();
  const [message, setMessage] = useState(false);
  const [validation, setValidation] = useState(false);
  const [allDept, setAllDept] = useState([]);
  const [allCountry, setAllCountry] = useState([]);
  const targetElementRef = useRef(null);

  const loggedUserId = localStorage.getItem("resId");

  const [routes, setRoutes] = useState([]);
  let textContent = "Vendors";
  let currentScreenName = ["Resource Vendor Management"];

  const ArrowRenderer = ({ expanded }) => (
    <>
      {expanded ? (
        <FaCaretDown className="chevronIcon" />
      ) : (
        <FaCaretDown className="chevronIcon" />
      )}
    </>
  );

  let alldepartments;
  let allcountries;

  const customValueRenderer = (selected, _options) => {
    return selected.length === country.length ||
      selected.length === departments.length
      ? "<< ALL >>"
      : selected.length === 0
      ? "<< Please Select >>"
      : selected.map((label) => {
          return selected.length > 1 ? label.label + "," : label.label;
        });
  };

  const initialValue = {
    buIds: "",
    country: "",
    fromDate: "",
    toDate: "",
    viewBy: "view",
  };

  const [formData, setFormData] = useState(initialValue);
  const baseUrl = environment.baseUrl;

  const getCountries = () => {
    axios
      .get(baseUrl + `/CostMS/cost/getCountries`)

      .then((Response) => {
        let countries = [];

        let data = Response.data;
        data.length > 0 &&
          data.forEach((e) => {
            let countryObj = {
              label: e.country_name,
              value: e.id,
            };
            countries.push(countryObj);
          });
        setCountry(countries);
        setSelectedCountry(countries);
      })
      .catch((error) => console.log(error));
  };

  useEffect(() => {
    let countryList = [];
    country.forEach((d) => {
      countryList.push(d.value);
    });
    setFormData((prevVal) => ({
      ...prevVal,
      ["country"]: countryList.toString(),
    }));
  }, [country]);

  const getDepartments = async () => {
    const resp = await axios({
      url: baseUrl + `/CostMS/cost/getDepartments`,
    });

    let departments = resp.data;
    departments.push({ value: 0, label: "Non-Revenue Units" });
    setDepartments(departments);
    setSelectedDepartments(departments.filter((ele) => ele.value != 0));
    let filteredDeptData = [];
    departments.forEach((data) => {
      if (data.value != 0) {
        filteredDeptData.push(data.value);
      }
    });
    setFormData((prevVal) => ({
      ...prevVal,
      ["buIds"]: filteredDeptData.toString(),
    }));
  };

  const getMenus = () => {
    // setMenusData

    axios({
      method: "GET",
      url: baseUrl + `/CommonMS/master/getMenus?loggedUserId=${loggedUserId}`,
    }).then((resp) => {
      let data = resp.data;

      data.forEach((item) => {
        if (item.display_name === textContent) {
          setRoutes([item]);
        }
      });
    });
  };

  useEffect(() => {
    getDepartments();
    getCountries();
    getMenus();
  }, []);

  const FromDateChangeHandler = (value, e) => {
    setStartDate(value);
    let frmdate = value;
    let currdate = new Date();

    frmdate > currdate ? setendDate(frmdate) : setendDate(currdate);
  };
  const ToDateChangeHandler = (value) => {
    setendDate(value);
  };
  let formattedFromDate = moment(startDate).format("YYYY-MM-DD");
  let toDateFromDate = moment(endDate).format("YYYY-MM-DD");

  useEffect(() => {}, [summaryData]);

  useEffect(() => {
    if (selectedDepartments.length === departments) {
      document.getElementsByClassName(
        "dropdown-heading-value"
      )[0].children[0].innerText = "<<All>>";
    }
  }, []);

  // need to write API call for summary table
  const searchHandle = () => {
    setVisible(!visible);
    visible
      ? setCheveronIcon(FaChevronCircleUp)
      : setCheveronIcon(FaChevronCircleDown);
    // setValidation("");
    // if (valid == true) {
    setValidation(true);
    // }

    // if (valid) {
    //   return;
    // }
    setFormDataNew(formData);
    let buDm = document.getElementById("bussinessUnit").children[0].children[0];
    let buDmCls = buDm.classList;

    let cuDm = document.getElementById("countryids").children[0].children[0];
    let cuDmCls = cuDm.classList;

    if (formData.buIds.length === 0) {
      buDmCls.contains("txtBoxBorderColor") === false &&
        buDmCls.add("txtBoxBorderColor");
    }
    if (formData.country.length === 0) {
      cuDmCls.contains("txtBoxBorderColor") === false &&
        cuDmCls.add("txtBoxBorderColor");
    }

    if (formData.buIds.length > 0) {
      buDmCls.contains("txtBoxBorderColor") &&
        buDmCls.remove("txtBoxBorderColor");
    }
    if (formData.country.length > 0) {
      cuDmCls.contains("txtBoxBorderColor") &&
        cuDmCls.remove("txtBoxBorderColor");
    }

    if (formData.buIds.length === 0 || formData.country.length === 0) {
      setValidator(true);
      return;
    }

    setValidator(false);
    setLoader(false);
    setOpen(false);

    const departmentArray = formData.buIds.split(",");

    alldepartments =
      departmentArray.length === departments.length ? "-1" : formData.buIds;
    setAllDept(alldepartments);

    const countryArray = formData.country.split(",");

    allcountries =
      countryArray.length === country.length ? "-1" : formData.country;

    setAllCountry(allcountries);
    abortController.current = new AbortController();

    axios({
      method: "post",
      url: baseUrl + `/VendorMS/management/getVendManagementSummary`,
      signal: abortController.current.signal,
      data: {
        buIds: alldepartments,
        country: allcountries,
        fromDate: formattedFromDate,
        toDate: toDateFromDate,
        isExport: 0,
      },

      headers: { "Content-Type": "application/json" },
    }).then((response) => {
      setSummaryData(response.data);
      setLoader(false);
      setTimeout(() => {
        setValidation(false);
      }, 3000);
      setOpen(true);
    });
  };

  const validationMs = (v) => {
    setValidation(v);
    scrollToTarget(targetElementRef);
  };

  const scrollToTarget = (targetElementRef) => {
    console.log(targetElementRef.current, "targetElementRef");
    var body = document.body,
      html = document.documentElement;
    var height = Math.max(
      body.scrollHeight,
      body.offsetHeight,
      html.clientHeight,
      html.scrollHeight,
      html.offsetHeight
    );
    if (targetElementRef.current) {
      targetElementRef.current.scrollIntoView({
        top: 0,
        behavior: "instant",
        block: "start",
        inline: "nearest",
      });
    }
  };
  console.log(summaryData, "summaryData");
  const abortController = useRef(null);
  const handleAbort = () => {
    abortController.current && abortController.current.abort();
    setRefresh(false);
  };
  return (
    <div>
      <div ref={targetElementRef}>
        {validation == "noChange" ? (
          <div className="statusMsg error">
            <AiFillWarning />
            No Modifications found to save
          </div>
        ) : validation == "notValid" ? (
          <div className="statusMsg error">
            <AiFillWarning />
            Please select valid values for highlighted fields
          </div>
        ) : (
          validation == "save" && (
            <div className="statusMsg success">
              <FaCheck /> Proposal saved successfully
            </div>
          )
        )}
      </div>
      {validator ? (
        <div className="errMsg">
          <span>Please Select Mandatory Fields</span>
        </div>
      ) : (
        ""
      )}

      <div className="col-md-12">
        <div className="pageTitle">
          <div className="childOne"></div>
          <div className="childTwo">
            <h2>Resource Vendor Management</h2>
          </div>
          <div className="childThree"></div>
        </div>
      </div>
      <ScreenBreadcrumbs
        routes={routes}
        currentScreenName={currentScreenName}
      />

      <div class="group mb-3 customCard">
        <div class="group-content row mb-2">
          <div className="col-md-12 collapseHeader">
            <h2>Search Filters</h2>
            <div className="helpBtn">
              <GlobalHelp />
            </div>
            <div
              onClick={() => {
                setVisible(!visible);
                visible
                  ? setCheveronIcon(FaChevronCircleUp)
                  : setCheveronIcon(FaChevronCircleDown);
              }}
            >
              <span>{cheveronIcon}</span>
            </div>
          </div>
          <CCollapse visible={!visible}>
            <>
              <div className="form-group row mt-2">
                <div className="col-md-3 mb-2 ">
                  <div className="row">
                    <label className="col-5 ">
                      Business Unit&nbsp;
                      <span className="required error-text">*</span>{" "}
                    </label>
                    <span className="col-1 p-0">:</span>
                    <div className="col-6 " id="bussinessUnit">
                      <MultiSelect
                        id="buIds"
                        ArrowRenderer={ArrowRenderer}
                        options={departments}
                        hasSelectAll={true}
                        isLoading={false}
                        shouldToggleOnHover={false}
                        disableSearch={false}
                        value={selectedDepartments}
                        valueRenderer={customValueRenderer}
                        disabled={false}
                        onChange={(s) => {
                          setSelectedDepartments(s);
                          let filteredValues = [];
                          s.forEach((d) => {
                            filteredValues.push(d.value);
                          });

                          setFormData((prevVal) => ({
                            ...prevVal,
                            ["buIds"]: filteredValues.toString(),
                          }));
                        }}
                      />
                    </div>
                  </div>
                </div>
                <div className="col-md-3 mb-2">
                  <div className="form-group row ">
                    <label className="col-5  ">
                      Country &nbsp;
                      <span className="required error-text">*</span>
                    </label>
                    <span className="col-1 p-0">:</span>
                    <div className="col-6">
                      <div id="countryids">
                        <MultiSelect
                          id="country"
                          ArrowRenderer={ArrowRenderer}
                          options={country}
                          hasSelectAll={true}
                          value={selectedCountry}
                          disabled={false}
                          valueRenderer={customValueRenderer}
                          onChange={(e) => {
                            setSelectedCountry(e);
                            let filteredCountry = [];
                            e.forEach((d) => {
                              filteredCountry.push(d.value);
                            });
                            setFormData((prevVal) => ({
                              ...prevVal,
                              ["country"]: filteredCountry.toString(),
                            }));
                          }}
                        />
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-md-3 mb-2 row">
                  <label className="col-5 ">
                    From &nbsp;<span className="required error-text">*</span>
                  </label>
                  <span className="col-1 p-0">:</span>
                  <div className="col-6 ">
                    <DatePicker
                      dateFormat="dd-MMM-yyyy"
                      selected={startDate}
                      showMonthDropdown
                      showYearDropdown
                      dropdownMode="select"
                      onChange={(value) => FromDateChangeHandler(value)}
                      onKeyDown={(e) => {
                        e.preventDefault();
                      }}
                    />
                  </div>
                </div>
                <div className="col-md-3 mb-2 row">
                  <label className="col-5 ">
                    To&nbsp;<span className="required error-text">*</span>{" "}
                  </label>
                  <span className="col-1 p-0">:</span>
                  <div className="col-6">
                    <DatePicker
                      dateFormat="dd-MMM-yyyy"
                      minDate={startDate}
                      selected={endDate}
                      showMonthDropdown
                      showYearDropdown
                      dropdownMode="select"
                      onChange={(value) => ToDateChangeHandler(value)}
                      onKeyDown={(e) => {
                        e.preventDefault();
                      }}
                    />
                  </div>
                </div>
                <div className="col-md-3 mb-2 ">
                  <div className="row">
                    <label className="col-5 ">Action</label>
                    <span className="col-1 p-0">:</span>
                    <div className="col-6 " id="action">
                      <select
                        onChange={(e) => {
                          setFormData((prevVal) => ({
                            ...prevVal,
                            ["viewBy"]: e.target.value,
                          }));
                        }}
                      >
                        <option value="view">View</option>
                        <option edit="edit">Edit</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>

              <div className="col-md-12 mb-2" align="center">
                <button className="btn btn-primary" onClick={searchHandle}>
                  <FaSearch />
                  Search
                </button>
              </div>
            </>
          </CCollapse>
        </div>

        {loader ? <Loader handleAbort={handleAbort} /> : ""}
        {refresh ? <Loader handleAbort={handleAbort} /> : ""}

        {open === true ? (
          <VendorManagSummaryTable
            setRefresh={setRefresh}
            refresh={refresh}
            formData={formDataNew}
            summaryDataNw={summaryData}
            formattedFromDate={formattedFromDate}
            toDateFromDate={toDateFromDate}
            validationM={validationMs}
            setValidation={setValidation}
            alldepartments={allDept}
            allcountries={allCountry}
            abortController={abortController}
          />
        ) : (
          ""
        )}
      </div>
    </div>
  );
}

export default VendorManagementFilters;
